<template>
  <div class="sprint-item-wrapper">
    <div class="sprint-date">
      <i class="fa fa-calendar"></i>
      <span>{{ sprint.startDate | dateFilter('MM月') }}</span>
    </div>
    <el-card class="sprint-card">
      <div class="sprint-header" slot="header">
        <div class="header-left">
          <i v-if="sprint.state === 3" class="fa fa-check-circle-o fa-lg margin-right-5"></i>
          <i v-else class="fa fa-play-circle fa-lg margin-right-5"></i>
          <span class="sprint-name">{{ sprint.name }}</span>
          <div class="sprint-start-date">
            <span>{{ sprint.startDate | dateFilter('yyyy年MM月dd日')}} ~</span>
            <span>{{ sprint.publishDate | dateFilter('yyyy年MM月dd日')}}</span>
          </div>
        </div>
        <div class="header-right">
          <span>导出：</span>
          <i class="fa fa-sort-numeric-asc" @click="onExportBacklog"></i>
          <i class="fa fa-tasks" @click="onExportTask"></i>
          <i class="fa fa-bug" @click="onExportBug"></i>
        </div>
      </div>
      <div class="sprint-detail">
        <div class="sprint-team">
          <span class="sprint-title">团队：</span>
          <span>{{ sprint.teams.length }}人</span>
          <img v-for="(item,index) in sprint.teams" :key="index" :src="headUrl + item.email" alt="">
        </div>
        <div class="sprint-value">
          <span class="sprint-title">价值：</span>
          <span>{{ sprint.value }}</span>
        </div>
        <div class="sprint-value">
          <span class="sprint-title">故事数量：</span>
          <span>{{ backlogCount }}</span>
        </div>
        <div class="sprint-value">
          <span class="sprint-title">缺陷数量：</span>
          <span>{{ bugCount }}</span>
        </div>
        <div class="sprint-value">
          <span class="sprint-title">计划会议：</span>
          <span v-for="(item, index) in sprint.planMeetings" :key="index">
            {{ item.date | dateFilter('yyyy年MM月dd日') }}
            {{ item.note }}；
          </span>
        </div>
        <div class="sprint-value">
          <span class="sprint-title">演示会议：</span>
          <span>{{ sprint.demoMeeting | dateFilter('yyyy年MM月dd日') }}</span>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      headUrl: this.$ccService.ccHeadUrl,
      bugCount: 0,
      backlogCount: 0
    }
  },
  props: [
    'sprint'
  ],
  methods: {
    initData () {
      this.findBugCount()
      this.findBacklogCount()
    },
    findBugCount () {
      let condition = {
        sprintId: this.sprint.id,
        state: 5
      }
      this.$dataService.findCount('bugs', condition).then(data => {
        this.bugCount = data
      })
    },
    findBacklogCount () {
      this.findCondition = {
        sprintId: this.sprint.id
      }
      this.findDataByCondition('backlogs').then(data => {
        this.backlogCount = data.length || 0
      })
    },
    onExportBacklog () {
      let condition = {
        query: {
          productId: this.sprint.productId,
          sprintId: this.sprint.id
        },
        sort: { 'important': -1 }
      }
      this.$http.post('/excel/export/backlog/rtc', condition, { responseType: 'arraybuffer' }).then((res) => {
        if (res && res.data) {
          this.downloadExcel(res.data, this.sprint.name + 'backlog.xls')
        }
      })
    },
    onExportTask () {
      let condition = {
        query: {
          productId: this.sprint.productId,
          sprintId: this.sprint.id
        },
        sort: { 'important': -1 }
      }
      this.$http.post('/excel/export/task/rtc', condition, { responseType: 'arraybuffer' }).then((res) => {
        if (res && res.data) {
          this.downloadExcel(res.data, this.sprint.name + 'task.xls')
        }
      })
    },
    onExportBug () {
      let condition = {
        query: {
          productId: this.sprint.productId,
          sprintId: this.sprint.id
        },
        sort: { 'createTime': -1 }
      }
      this.$http.post('/excel/export/bug/rtc', condition, { responseType: 'arraybuffer' }).then((res) => {
        if (res && res.data) {
          this.downloadExcel(res.data, this.sprint.name + 'bug.xls')
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .sprint-item-wrapper{
    padding-bottom: 10px;
    margin-bottom: 10px;
    border-bottom: 1px solid #eee;
    display: flex;

    .sprint-date{
      flex: 0 0 100px;
      margin: 10px 0 0 10px;
    }

    .sprint-card{
      margin-right: 30px;
      width: 200px;
      flex: 1 1 auto;
      display: flex;
      flex-direction: column;
    }

    .sprint-header{
      display: flex;
      align-items: center;

      .header-left{
        flex:  1 1 300px;
        display: flex;
        align-items: center;
      }

      .header-right{
        .fa {
          margin-right: 5px;
          color: #aaa;
          cursor: pointer;
        }

        .fa:hover {
          color: #333;
        }
      }

      .fa-check-circle-o{
        color: #409EFF;
      }

      .fa-play-circle{
        color: #67C23A;
      }

      .sprint-name{
        font-size: 16px;
      }

      .sprint-start-date{
        font-size: 14px;
        color: #aaa;
        margin-left: 10px;
      }
    }
    .sprint-detail{
      font-size: 14px;
      line-height: 26px;
      display: flex;
      flex-direction: column;

      .sprint-value{
        font-size: 14px;
        color: #444;
      }

      img{
        width: 24px;
        border-radius: 15px;
        margin-left: 5px;
      }

      .sprint-team{
        line-height: 24px;
        display: flex;
        align-items: center;
      }

      .sprint-title{
        color: #999;
      }
    }
  }
</style>



